export function el(selector, root) {
  const el2 = (root ?? document).querySelector(selector);
  if (!el2)
    throw Error(`No element ${selector}`);
  return el2;
}
export function using(results) {
  return {
    do: (fn) => {
      if (results !== void 0 && results !== null)
        return fn(results);
    }
  };
}
export function listen(element) {
  return {
    on: (type) => ({
      do: (listener) => {
        element.addEventListener(type, listener);
        return () => element.removeEventListener(type, listener);
      }
    })
  };
}
